import React from 'react';
import { Container } from 'react-bootstrap';
import FadeIn from 'react-fade-in';
import { Gmaps, Marker, InfoWindow, Circle } from 'react-gmaps';
import white1 from '../images/white1.jpg';

function Contact() {
  const coords = {
    lat: -7.354584625455096,
    lng: 112.74855696931235,
  };

  const params = { v: '3.exp', key: 'AIzaSyAktinfy5Mtzzc_Rp_2aaNM3GAx74MTfBc' };
  const onMapCreated = (map) => {
    map.setOptions({
      disableDefaultUI: true,
    });
  };
  return (
    <FadeIn>
      <div
        className="col-md-12"
        style={{
          backgroundImage: `url(${white1})`,
          backgroundPosition: 'center',
          backgroundSize: 'cover',
          backgroundRepeat: 'noRepeat',
          opacity: '100%',
          paddingTop: '50px',
          color: 'gray',
        }}
      >
        {window.innerWidth > 760 && (
          <Container style={{ marginTop: '7%', paddingBottom: '10%' }}>
            <text style={{ fontSize: '30px' }}>Contact Me</text>
            <div className="row" style={{ marginTop: '10%' }}>
              <div className="col-md-6" style={{ textAlign: 'left' }}>
                <table style={{ marginTop: '10%', marginBottom: '10%' }}>
                  <tr>
                    <td style={{ width: '15%', textAlign: 'center' }}>
                      <i class="fa fa-map-marker" aria-hidden="true"></i>
                    </td>
                    <td>Address</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>
                      Perumahan Grand Deltasari
                      <br />
                      Aster No 71
                      <br />
                      Waru | 61256
                      <br />
                    </td>
                  </tr>
                  <br />

                  <tr>
                    <td style={{ width: '15%', textAlign: 'center' }}>
                      <i class="fa fa-phone" aria-hidden="true"></i>{' '}
                    </td>
                    <td>Let's Talk</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>
                      <a href="tel:082228032542">082228032542</a>
                    </td>
                  </tr>
                  <br />

                  <tr>
                    <td style={{ width: '15%', textAlign: 'center' }}>
                      <i class="fa fa-envelope" aria-hidden="true"></i>
                    </td>
                    <td>E-Mail</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>
                      <a href="mailto:ramaadhiguna@gmail.com">
                        ramaadhiguna@gmail.com
                      </a>
                    </td>
                  </tr>
                </table>
              </div>
              <div className="col-md-6">
                <Gmaps
                  width={'100%'}
                  height={'100%'}
                  lat={coords.lat}
                  lng={coords.lng}
                  zoom={12}
                  loadingMessage={'Be happy'}
                  params={params}
                  onMapCreated={() => onMapCreated}
                >
                  <Marker
                    lat={coords.lat}
                    lng={coords.lng}
                    draggable={true}
                    //onDragEnd={this.onDragEnd}
                  />
                  <InfoWindow
                    lat={coords.lat}
                    lng={coords.lng}
                    content={'My Sweet Home'}
                    //onCloseClick={this.onCloseClick}
                  />
                  <Circle
                    lat={coords.lat}
                    lng={coords.lng}
                    radius={500}
                    //onClick={this.onClick}
                  />
                </Gmaps>
              </div>
            </div>
          </Container>
        )}
        {window.innerWidth <= 760 && (
          <FadeIn>
            <Container style={{ marginTop: '15%', paddingBottom: '15%' }}>
              <text style={{ fontSize: '30px' }}>Contact Me</text>
              <div className="col-md-12" style={{ textAlign: 'left' }}>
                <table style={{ marginTop: '10%', marginBottom: '10%' }}>
                  <tr>
                    <td style={{ width: '15%', textAlign: 'center' }}>
                      <i class="fa fa-map-marker" aria-hidden="true"></i>
                    </td>
                    <td>Address</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>
                      Perumahan Grand Deltasari
                      <br />
                      Aster No 71
                      <br />
                      Waru | 61256
                      <br />
                    </td>
                  </tr>
                  <br />

                  <tr>
                    <td style={{ width: '15%', textAlign: 'center' }}>
                      <i class="fa fa-phone" aria-hidden="true"></i>{' '}
                    </td>
                    <td>Let's Talk</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>
                      <a href="tel:082228032542">082228032542</a>
                    </td>
                  </tr>
                  <br />

                  <tr>
                    <td style={{ width: '15%', textAlign: 'center' }}>
                      <i class="fa fa-envelope" aria-hidden="true"></i>
                    </td>
                    <td>E-Mail</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>
                      <a href="mailto:ramaadhiguna@gmail.com">
                        ramaadhiguna@gmail.com
                      </a>
                    </td>
                  </tr>
                </table>
              </div>
              <div className="border boder-dark col-md-12">
                <Gmaps
                  width={'320px'}
                  height={'400px'}
                  lat={coords.lat}
                  lng={coords.lng}
                  zoom={12}
                  loadingMessage={'Be happy'}
                  params={params}
                  onMapCreated={() => onMapCreated}
                >
                  <Marker
                    lat={coords.lat}
                    lng={coords.lng}
                    draggable={true}
                    //onDragEnd={this.onDragEnd}
                  />
                  <InfoWindow
                    lat={coords.lat}
                    lng={coords.lng}
                    content={'My Sweet Home'}
                    //onCloseClick={this.onCloseClick}
                  />
                  <Circle
                    lat={coords.lat}
                    lng={coords.lng}
                    radius={500}
                    //onClick={this.onClick}
                  />
                </Gmaps>
              </div>
            </Container>
          </FadeIn>
        )}
      </div>
    </FadeIn>
  );
}

export default Contact;
