import React from 'react';
import { Container, Card, Accordion, Button } from 'react-bootstrap';
import FadeIn from 'react-fade-in';
import iam from '../images/iam.png';
import white from '../images/white1.jpg';
import guitar from '../images/guitar.png';
import keyboard from '../images/keyboard1.png';

function About() {
  return (
    <FadeIn>
      <div
        style={{
          backgroundImage: `url(${white})`,
          backgroundPosition: 'center',
          backgroundSize: 'cover',
          backgroundRepeat: 'noRepeat',
          paddingTop: '5%',
          paddingBottom: '3%',
          color: 'gray',
        }}
      >
        {window.innerWidth <= 760 && (
          <Container>
            <Accordion>
              <Card className="bg-dark" style={{ marginTop: '20%' }}>
                <Card.Img src={iam} alt="Card image" />
                <Accordion.Toggle as={Accordion} variant="dark" eventKey="0">
                  <Card.ImgOverlay>
                    <div className="row">
                      <div
                        className="col-md-5"
                        style={{
                          textAlign: 'justify',
                          color: 'lightgray',
                          paddingTop: '20%',
                        }}
                      >
                        <text style={{ fontSize: '20px' }}>
                          My Name is Rama
                        </text>
                        <br />
                      </div>
                      <div className="col-md-7"></div>
                    </div>
                  </Card.ImgOverlay>
                </Accordion.Toggle>
                <Accordion.Collapse eventKey="0">
                  <Card.Body>
                    <text style={{ fontSize: '20px' }}>
                      A software engineer by trade. Programming is always been
                      my passion since I was in university. Right now, MERN
                      stack programing really does piqué my interest. I also
                      love to play guitar, electric or acoustic. I usually plays
                      metal genre, but I also plays acoustic for time to time.
                      I'm also a Mechanical Keyboard Enthusiast, really love
                      that <i>thock</i> feeling when I types. And for now, I
                      already reach my <i>end-game</i> board.
                    </text>
                  </Card.Body>
                </Accordion.Collapse>
              </Card>
            </Accordion>

            <Accordion>
              <Card className="bg-dark" style={{ marginTop: '10%' }}>
                <Card.Img src={guitar} alt="Card image" />
                <Accordion.Toggle as={Accordion} variant="dark" eventKey="0">
                  <Card.ImgOverlay>
                    <div className="row">
                      <div
                        className="col-md-12"
                        style={{
                          textAlign: 'right',
                          color: 'lightgray',
                          paddingTop: '25%',
                        }}
                      >
                        <text style={{ fontSize: '20px' }}>
                          Six strings is all i need
                        </text>
                        <br />
                      </div>
                    </div>
                  </Card.ImgOverlay>
                </Accordion.Toggle>
                <Accordion.Collapse eventKey="0">
                  <Card.Body>
                    <text style={{ fontSize: '20px' }}>
                      A distinctive sound, high-gain, distorted, palm-muted,
                      low-pitch of a heavenly Lyre. I just can't get enough of
                      this. My love for this genre is unmeasurable, but so does
                      my love to acoustic guitar. When it comes to electric
                      guitar, i always trusted my Ibanez RGD321 with a flick of
                      NeuralDSP Cali and Plini. When it comes to acoustic, my
                      old buddy Seagull Excursion will go with me all night
                      long.
                    </text>
                  </Card.Body>
                </Accordion.Collapse>
              </Card>
            </Accordion>
            <Accordion style={{ paddingBottom: '5%' }}>
              <Card className="bg-dark" style={{ marginTop: '10%' }}>
                <Card.Img src={keyboard} alt="Card image" />
                <Accordion.Toggle as={Accordion} variant="dark" eventKey="0">
                  <Card.ImgOverlay>
                    <div className="row">
                      <div
                        className="col-md-5"
                        style={{
                          textAlign: 'justify',
                          color: 'lightgray',
                          paddingTop: '20%',
                        }}
                      >
                        <text style={{ fontSize: '20px' }}>
                          <i>Thock</i> is the only way
                        </text>
                        <br />
                      </div>
                      <div className="col-md-7"></div>
                    </div>
                  </Card.ImgOverlay>
                </Accordion.Toggle>
                <Accordion.Collapse eventKey="0">
                  <Card.Body>
                    <text style={{ fontSize: '20px' }}>
                      Ah yes, <b>Gateron Ink v2 Silent Black</b>. You got these
                      bad boy lubed and filmed, these switches will going
                      places. Since GMK only offers ABS keycaps, a GMK cloned
                      thick PBT keycaps would do just fine. A base board that
                      offers premium build, quality bluetooth connection, and
                      compact? well that's just a cherry on top. What happen
                      when you combine these? Well you get yourself my{' '}
                      <i>end-game</i> thock board.
                    </text>
                  </Card.Body>
                </Accordion.Collapse>
              </Card>
            </Accordion>
          </Container>
        )}
        {window.innerWidth > 760 && (
          <Container>
            <Card className="bg-dark" style={{ marginTop: '2%' }}>
              <Card.Img src={iam} alt="Card image" />
              <Card.ImgOverlay>
                <div className="row">
                  <div
                    className="col-md-5"
                    style={{
                      textAlign: 'justify',
                      color: 'lightgray',
                      paddingTop: '11%',
                    }}
                  >
                    <h2>My Name is Rama</h2>
                    <br />
                    <text style={{ fontSize: '20px' }}>
                      A software engineer by trade. Programming is always been
                      my passion since I was in university. Right now, MERN
                      stack programing really does piqué my interest. I also
                      love to play guitar, electric or acoustic. I usually plays
                      metal genre, but I also plays acoustic for time to time.
                      I'm also a Mechanical Keyboard Enthusiast, really love
                      that <i>thock</i> feeling when I types. And for now, I
                      already reach my <i>end-game</i> board.
                    </text>
                  </div>
                  <div className="col-md-7"></div>
                </div>
              </Card.ImgOverlay>
            </Card>
            <Card className="bg-dark" style={{ marginTop: '3%' }}>
              <Card.Img src={guitar} alt="Card image" />
              <Card.ImgOverlay>
                <div className="row">
                  <div className="col-md-7"></div>
                  <div
                    className="col-md-5"
                    style={{
                      textAlign: 'justify',
                      color: 'lightgray',
                      paddingTop: '11%',
                    }}
                  >
                    <h2>Six strings is all i need</h2>
                    <br />
                    <text style={{ fontSize: '20px' }}>
                      A distinctive sound, high-gain, distorted, palm-muted,
                      low-pitch of a heavenly Lyre. I just can't get enough of
                      this. My love for this genre is unmeasurable, but so does
                      my love to acoustic guitar. When it comes to electric
                      guitar, i always trusted my Ibanez RGD321 with a flick of
                      NeuralDSP Cali and Plini. When it comes to acoustic, my
                      old buddy Seagull Excursion will go with me all night
                      long.
                    </text>
                  </div>
                </div>
              </Card.ImgOverlay>
            </Card>
            <Card className="bg-dark" style={{ marginTop: '3%' }}>
              <Card.Img src={keyboard} alt="Card image" />
              <Card.ImgOverlay>
                <div className="row">
                  <div
                    className="col-md-5"
                    style={{
                      textAlign: 'justify',
                      textAlignLast: 'left',
                      color: 'lightgray',
                      paddingTop: '9%',
                    }}
                  >
                    <h2>
                      <i>Thock</i> is the only way
                    </h2>
                    <br />
                    <text style={{ fontSize: '20px' }}>
                      Ah yes, <b>Gateron Ink v2 Silent Black</b>. You got these
                      bad boy lubed and filmed, these switches will going
                      places. Since GMK only offers ABS keycaps, a GMK cloned
                      thick PBT keycaps would do just fine. A base board that
                      offers premium build, quality bluetooth connection, and
                      compact? well that's just a cherry on top. What happen
                      when you combine these? Well you get yourself my{' '}
                      <i>end-game</i> thock board.
                    </text>
                  </div>
                  <div className="col-md-7"></div>
                </div>
              </Card.ImgOverlay>
            </Card>
          </Container>
        )}
      </div>
    </FadeIn>
  );
}

export default About;
