import React from 'react';
import { Container, Button, Accordion } from 'react-bootstrap';
import FadeIn from 'react-fade-in';
import tirta from '../images/tirta.jpg';
import tajuk from '../images/tajuk.jpeg';
import cv from '../files/Ciruculum Vitae.pdf';
import white1 from '../images/white1.jpg';
function Career() {
  return (
    <div
      className="col-md-12"
      style={{
        backgroundImage: `url(${white1})`,
        backgroundPosition: 'center',
        backgroundSize: 'cover',
        backgroundRepeat: 'noRepeat',
        paddingTop: '5%',
        color: 'gray',
        paddingBottom: '5%',
      }}
    >
      {window.innerWidth > 760 && (
        <FadeIn>
          <Container>
            <div className="col-md-12" style={{ paddingBottom: '3%' }}>
              <text style={{ fontSize: '30px' }}>My Career</text>
            </div>
            <div className="row">
              <div
                className="col-md-6 "
                style={{
                  textAlign: 'right',
                  alignItems: 'center',
                  marginTop: '1.5%',
                }}
              >
                <u style={{ fontSize: '30px' }}>2017-2018</u>
                <br />
                <text style={{ fontSize: '30px' }}>CV Tajuk</text>
                <p>
                  I was an intern with my friend Sonny from August 2017 to
                  February 2018. Here we were given the task of developing a
                  website-based information system to help the company's
                  operational processes. the system we have developed includes
                  sales orders, purchases orders, and production. We are also in
                  charge of providing training for all the people and support
                  for the operation of this system in the company. I am very
                  grateful to this company, because it has given me a lot of
                  knowledge.
                </p>
              </div>
              <div className="col-md-6 border-left border-dark">
                <img
                  src={tajuk}
                  style={{ maxWidth: '100%', borderRadius: '3%' }}
                />
              </div>
            </div>
            <div className="row">
              <div
                className="col-md-6"
                style={{
                  position: 'relative',
                  top: '50%',
                }}
              >
                <img
                  src={tirta}
                  style={{
                    marginTop: '7%',
                    maxWidth: '100%',
                    borderRadius: '3%',
                  }}
                />
              </div>
              <div
                className="col-md-6 border-left border-dark"
                style={{ textAlign: 'left' }}
              >
                <div style={{ marginTop: '6%' }}>
                  <u style={{ fontSize: '30px' }}>2018-2020</u>
                  <br />
                  <text style={{ fontSize: '30px' }}>
                    PT Tirtakencana Tatawarna
                  </text>
                  <p>
                    Me and my friend, Billie started working in this company on
                    October 1st 2018. I was assigned as software engineer and
                    end-user troubleshooter for Microsoft Dynamic NAV system. As
                    a software engineer, I create additional desktop and
                    website-based utilities for NAV, and sometimes I create
                    systems to support internal work for the team. I also
                    created a website-based file management system for the HR
                    Management System (Orange). I am very happy to have worked
                    here, all of my colleagues are very pleasant to work with.
                    My boss also taught me many things. I will never forget my
                    memories with my friends in this company.
                  </p>
                </div>
              </div>
            </div>
            <div style={{ paddingTop: '5%' }}>
              <a href={cv} download="Curiculum Vitae">
                <Button variant="danger">
                  <i className="fa fa-download"></i> | Curiculum Vitae
                </Button>
              </a>
            </div>
          </Container>
        </FadeIn>
      )}
      {window.innerWidth <= 760 && (
        <FadeIn>
          <Container>
            <div
              className="col-md-12"
              style={{ paddingBottom: '10%', marginTop: '20%' }}
            >
              <text style={{ fontSize: '25px' }}>My Career</text>
            </div>
            <Accordion>
              <div className="col-md-6 ">
                <u style={{ fontSize: '20px' }}>2017-2018</u>
                <br />
                <Accordion.Toggle as={Button} variant="link" eventKey="0">
                  <img
                    src={tajuk}
                    style={{ maxWidth: '100%', borderRadius: '3%' }}
                  />
                </Accordion.Toggle>
                <Accordion.Collapse eventKey="0">
                  <div>
                    <text style={{ fontSize: '20px' }}>CV Tajuk</text>
                    <p>
                      I was an intern with my friend Sonny from August 2017 to
                      February 2018. Here we were given the task of developing a
                      website-based information system to help the company's
                      operational processes. the system we have developed
                      includes sales orders, purchases orders, and production.
                      We are also in charge of providing training for all the
                      people and support for the operation of this system in the
                      company. I am very grateful to this company, because it
                      has given me a lot of knowledge.
                    </p>
                  </div>
                </Accordion.Collapse>
              </div>
            </Accordion>

            <Accordion style={{ paddingTop: '5%' }}>
              <div className="col-md-6 ">
                <u style={{ fontSize: '20px' }}>2018-2020</u>
                <br />
                <Accordion.Toggle as={Button} variant="link" eventKey="0">
                  <img
                    src={tirta}
                    style={{ maxWidth: '100%', borderRadius: '3%' }}
                  />
                </Accordion.Toggle>
                <Accordion.Collapse eventKey="0">
                  <div>
                    <text style={{ fontSize: '20px' }}>
                      PT Tirtakencana Tatawarna
                    </text>
                    <p>
                      Me and my friend, Billie started working in this company
                      on October 1st 2018. I was assigned as software engineer
                      and end-user troubleshooter for Microsoft Dynamic NAV
                      system. As a software engineer, I create additional
                      desktop and website-based utilities for NAV, and sometimes
                      I create systems to support internal work for the team. I
                      also created a website-based file management system for
                      the HR Management System (Orange). I am very happy to have
                      worked here, all of my colleagues are very pleasant to
                      work with. My boss also taught me many things. I will
                      never forget my memories with my friends in this company.
                    </p>
                  </div>
                </Accordion.Collapse>
                <div style={{ paddingTop: '5%' }}>
                  <small>
                    <i>Click image to expand</i>
                  </small>
                </div>
              </div>
            </Accordion>
            <div style={{ paddingTop: '10%', paddingBottom: '10%' }}>
              <a href={cv} download="Curiculum Vitae">
                <Button variant="danger">
                  <i className="fa fa-download"></i> | Curiculum Vitae
                </Button>
              </a>
            </div>
          </Container>
        </FadeIn>
      )}
    </div>
  );
}

export default Career;
